  src: url("../fonts/segoe_ui.eot?#iefix") format("embedded-opentype"), url("../fonts/segoe_ui.woff2") format("woff2"), url("../fonts/segoe_ui.woff") format("woff"), url("../fonts/segoe_ui.ttf") format("truetype"), url("../fonts/segoe_ui.svg#segoe_ui") format("svg");
