.container {
  max-width: 1495px;
  margin: 0 auto;
  padding: 0 15px;
}

.header-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 50px;
}
.header-nav_menu {
  display: flex;
  align-items: center;
  gap: 50px;
}
.header-nav_menu-search {
  display: flex;
  justify-content: center;
  position: relative;
  align-items: center;
  width: 49px;
  height: 49px;
  border-radius: 50%;
  background: #09B3B0;
}
.header-nav__menu-link {
  font-weight: 400;
  font-size: 18px;
  color: #09B3B0;
  text-decoration: none;
}
.header-nav__menu-link:hover {
  text-decoration: underline;
  color: #FF9F00;
}
.header-nav__menu-btn {
  font-weight: 500;
  font-size: 18px;
  height: 50px;
  width: 99px;
  border: 5px solid #09B3B0;
  border-radius: 10px;
  background-color: #09B3B0;
  color: #FFFFFF;
}

.magnifying-shadow {
  top: 15px;
  position: absolute;
}

.header-content {
  background-image: url(/img/bg.jpg);
  background-size: cover;
  height: 100%;
}
.header-content_wrapper {
  text-align: center;
  padding: 120px 923px 120px 0;
}
.header-content_wrapper-title {
  font-family: "Winter Insight", sans-serif;
  font-weight: 400;
  font-size: 135px;
  color: #FF9F00;
}
.header-content_wrapper-description {
  font-family: "Winter Insight", sans-serif;
  font-weight: 400;
  font-size: 30px;
  text-transform: uppercase;
}
.header-content_wrapper-price {
  font-family: "Yrsa, sans-serif";
  font-weight: 400;
  font-size: 36px;
  text-transform: lowercase;
  color: #FF9F00;
}

.content-advisor_bg {
  background-image: url(/img/advisor-bg.jpg);
}

.content-advisor_wrapper {
  padding: 60px 0;
}

.content-advisor {
  display: flex;
  justify-content: space-between;
  padding-bottom: 50px;
}

.content-advisor_heading {
  font-family: "Yrsa, sans-serif";
  font-weight: 400;
  font-size: 72px;
  text-align: center;
  color: #FFFFFF;
}

.content-advisor_heading-bg-color {
  padding: 50px 0;
  background-color: #09B3B0;
}

.content-advisor_left {
