}

.content-advisor_left-description {
  max-width: 458px;
  font-weight: 400;
  font-size: 16px;
  color: #09292F;
}

.content-advisor_left-info {
  display: flex;
  flex-direction: column;
  gap: 23px;
}

.content-advisor_right {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.content-advisor_right-freeTime {
  font-weight: 400;
  font-size: 12px;
  padding: 6px 49px 6px 8px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
